.start {
  text-align: center;
  position: relative;
  z-index: 2;
  transition: opacity 200ms;
}
.start h1 {
  text-decoration: underline;
}
.ban {
  display: inline-block;
  width: 200px;
  cursor: pointer;
}
.renderBox {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 20px;
  opacity: 0;
  z-index: 1;
  transition: opacity 1000ms;
}